<template>
  <div :class="classes">
    <span>{{ progress }}%</span>
    <div class="left-half-clipper">
      <div class="first50-bar"/>
      <div class="value-bar"/>
    </div>
  </div>
</template>


<script>
export default {
  name: 'CircleProgressbar',
  props: {
    progress: {
      type: Number,
      required: true
    }
  },
  computed: {
    classes () {
      let arr = 'progress-circle'

      if (this.progress > 50) {
        arr += ' over50'
      }

      arr += ` p${Math.round(this.progress)}`

      return arr
    }
  }
}
</script>


<style scoped>
